#soundsphere {
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: -1; }
  #soundsphere #canvas-wrapper {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 100%;
    height: 100%;
    max-width: 500px;
    max-height: 500px;
    transform: translate(-50%, -50%); }
    #soundsphere #canvas-wrapper #canvas,
    #soundsphere #canvas-wrapper #canvas-ascii {
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0px; }
    #soundsphere #canvas-wrapper #canvas {
      visibility: hidden; }
    #soundsphere #canvas-wrapper #canvas-ascii {
      font-size: 4px; }
