#audio-player {
  position: fixed;
  bottom: 0px;
  right: 0px;
  margin: 10px;
  z-index: 100; }
  #audio-player #audio-info {
    display: inline-block;
    position: relative;
    vertical-align: middle;
    left: 10px;
    margin-right: 10px;
    transition: 0.3s;
    opacity: 0; }
    #audio-player #audio-info.show {
      left: 0px;
      opacity: 1; }
    #audio-player #audio-info #audio-title,
    #audio-player #audio-info #audio-author {
      display: block;
      font-family: Arial;
      font-size: 13px;
      text-align: right; }
    #audio-player #audio-info #audio-author {
      font-size: 11px; }
  #audio-player #sound-toggle,
  #audio-player #live-input-toggle {
    width: 30px;
    height: 30px;
    vertical-align: middle;
    opacity: 0.7;
    cursor: pointer; }
  #audio-player #live-input-toggle {
    position: fixed;
    bottom: 0px;
    left: 0px;
    margin: 10px; }
